@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  @apply scroll-smooth focus:scroll-auto;
}

.card {
  @apply bg-gradient-to-br
from-vintageWhite
to-lightGray
  bg-opacity-50
  w-screen
  1180px:w-fit
  inline-block
  border-8
border-white
  rounded-sm
  overflow-hidden
  shadow-md
  mb-2
  mx-2
  px-4
  1180px:mb-4;
}

.card-title {
  @apply font-bold 
  font-specialElite
    text-lg 
    px-3 
    pb-3;
}

.card img {
  @apply mx-3 mt-3 1180px:max-w-xs;
}

.badge {
  @apply bg-gradient-to-br
  from-secondary
  to-pink-600 
    text-white 
    rounded-full 
    text-sm 
    ml-7 
    px-3 
    py-2 
    mt-1 
    shadow-inner
    absolute
    hover:animate-pulse;
}

.btn {
  @apply rounded-full py-2 px-3 uppercase text-xs font-bold cursor-pointer tracking-wider;
}

.navbar {
  @apply bg-whiteSmoke py-3 text-xl shadow-xl;
}

.navbar-item {
  @apply text-sm
    md:text-lg
    m-2
    md:m-4
    inline-block
    transition
    ease-out
    duration-500
    border-b
    border-b-transparent
    hover:text-secondary
    hover:border-b
    hover:border-b-secondary
    hover:-translate-y-0.5;
}

section {
  @apply my-12 py-12;
}

#Education {
  @apply pb-12;
}

.section-title {
  @apply text-3xl
         text-primary
           font-bold 
           w-fit
           block 
           border-b-2 
         border-primary 
           pr-12
           mb-10;
}

.contact-item {
  @apply text-sm
    md:text-lg 
    text-trietary-dark
    inline-block
    mr-2
    mb-2
    md:mr-6
    transition
    ease-out
    duration-500
    border-b
    border-b-transparent
    hover:text-secondary
    hover:border-b
    hover:border-b-secondary
    hover:cursor-pointer
    hover:-translate-y-0.5;
}

.contact-item svg {
  @apply inline-block w-6 h-6 pb-1 pr-1 motion-safe:group-hover:animate-wiggle;
}

.contact-item a {
  @apply inline-block;
}

.container {
  @apply w-full max-w-7xl my-0 mx-auto py-0 px-10 box-border;
}

p {
  @apply indent-7 text-justify;
}

.education-item .item-title {
  @apply text-lg font-bold inline-block;
}

.education-item .item-info {
  @apply text-trietary-dark text-sm;
}

.education-item .item-dates {
  @apply italic;
}

.custom-button {
  @apply border 
    border-trietary-dark 
    rounded-full 
    px-4 
    py-2
    transition
    ease-out
    duration-500

    hover:bg-gradient-to-br
  hover:from-secondary
  hover:to-pink-600 

    hover:text-white
    hover:border-white
    hover:bg-secondary
    hover:shadow-md
    cursor-pointer;
}

.achievement-short {
  @apply inline-block 
    pr-6 
    py-2 
    last:pr-0;
}

.achievement-short .achievement-number {
  @apply text-4xl text-secondary font-calligraphy inline-block;
}

.achievement-short .achievement-description {
  @apply inline-block pl-1;
}

